<script>
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'VoucherDetails',
  props: {
    rowData: {
      type: Object,
      required: true,
    },
    rowIndex: {
      type: Number,
    },
  },
  data () {
    return {
      details: [],

    }
  },

  mounted () {
    this.getVoucherDetails()
  },

  methods: {
    getVoucherDetails () {
      this.$httpClient.get('/datatable/voucherDetails', {
        params: {
          filter: {
            voucher_id: this.rowData.id,
          },
        },
      })
        .then(({ data }) => {
          this.details = data.data
        }).catch((error) => {
          console.error('Voucher details fetching failed: ' + error.message)
        })
    },
  },
})
</script>

<template>
  <div class="row">
    <div class="col-xl-1" />
    <div class="col-xl-6">
      <table class="styled-table">
        <thead>
          <tr>
            <th class="ps-3" style="width: 60%">
              Description
            </th>
            <th class="text-end pe-3" style="width: 15%">
              Price
            </th>
            <th class="text-center" style="width: 10%">
              Qty
            </th>
            <th class="text-end pe-3" style="width: 15%">
              Amount
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="transaction in details" :key="transaction.id">
            <td class="text-truncate ps-3">
              {{ transaction.description }}
            </td>
            <td class="text-end pe-3">
              {{ transaction.price | toMoneyFormat }}
            </td>
            <td class="text-center">
              {{ transaction.quantity }}
            </td>
            <td class="text-end pe-3">
              {{ transaction.amount | toMoneyFormat }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

